<script>
import AppBar from '@/components/AppBar';
import HomeStatus from '@/components/home/HomeStatus';
import HomeInfo from '@/components/home/HomeInfo';
import moment from 'moment';
export default {
  name: 'Home',
  components: {HomeInfo, HomeStatus, AppBar},
  created() {
    if (this.$store.state.ficha.cadastroData && this.$store.state.startTime) {
      const diffStart = moment().diff(this.$store.state.startTime, 'seconds');
      const diffUpdate = moment().diff(this.$store.state.ficha.cadastroData, 'seconds');
      if (diffUpdate >= 60 && diffStart >= 60) this.$store.dispatch('ficha/reloadCadastro');
    }
  },
}
</script>
